import React from 'react';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router';
import { Alert, Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { SINTETICO_ANALITICO_ENUM_VALUES } from '../../../../../models/enumerated/mlf/sinteticoAnaliticoEnum';
import { TIPO_ORDENACAO_FORCLI_ENUM_VALUES } from '../../../../../models/enumerated/mlf/tipoOrdenacaoForcliEnum';
import ReportFormView from '../../../report/ReportFormView';

interface ForcliRelPass {
  idGerEmpresa?: any;
  ordenacao?: any;
  fornecedor?: boolean;
  cliente?: boolean;
  transportador?: boolean;
  operadoraCartao?: boolean;
  idGrupoForcli?: any;
  tipo?: string;
}

const REPORT_TITLE = 'Relação de Clientes/Fornecedores/Transportadoras/Operadoras de Cartão';
const REPORT_EVENT: string = 'printRelForcli'; // Nome do metódo a ser chamado no backend
const CLASS_KEY: string = 'forclirelpass'; // Nome da classe RelPass no backedn

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY),
  dtFinal: '01/05/2020',
  dtInicial: '01/05/2019',
};

export default function ForcliRelacaoCompleta(props: WindowComponentProps) {
  const history = useHistory();
  const formProps = useForm<ForcliRelPass>({ defaultValues: { idGerEmpresa: '1489716', idGrupoForcli: '1489723' } });

  function validateExtras(parameter: PrinterDataBasic, { cliente, fornecedor, transportador, operadoraCartao }: ForcliRelPass): string[] {
    const erros = [];
    if (!cliente && !fornecedor && !transportador && !operadoraCartao) {
      erros.push('No mínimo um item deve ser selecionado: Fornecedor/Cliente/Transportadora/Operadora de Cartão');
    }
    return erros;
  }

  return (
    <WindowComponent {...props} title={REPORT_TITLE}>
      <Alert variant="danger" className="text-center">
        Obs: A listagem será feita pela interseção dos valores selecionados no campo Tipo.
      </Alert>
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history} validateExtras={validateExtras}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <InputGroupInline formProps={formProps} id="idGrupoForcli" hidden={true} />
        <InputGroupInline
          id="ordenacao"
          label="Ordenação"
          isPrependLabel={true}
          placeholder="Selecione o tipo de ordenação"
          formProps={{ ...formProps, validation: { required: true } }}
          inputDropDownProps={{
            optionsProvider: TIPO_ORDENACAO_FORCLI_ENUM_VALUES,
            selectProps: { isClearable: false },
          }}
          type={InputType.DROP_DOWN}
        />

        <InputGroupInline
          id="tipo"
          label="Forma"
          isPrependLabel={true}
          placeholder="Selecione o tipo de geração do relatório"
          formProps={{ ...formProps, validation: { required: true } }}
          inputDropDownProps={{
            optionsProvider: SINTETICO_ANALITICO_ENUM_VALUES,
            selectProps: { isClearable: false },
          }}
          type={InputType.DROP_DOWN}
        />

        <Row className="align-items-center">
          <Col xs={1}>
            <strong>Tipo:</strong>
          </Col>
          <Col xs={2}>
            <CustomInput type="switch" label="Fornecedor" id="fornecedor" name="fornecedor" innerRef={formProps.register} />
          </Col>
          <Col xs={2}>
            <CustomInput type="switch" label="Cliente" id="cliente" name="cliente" innerRef={formProps.register} />
          </Col>
          <Col xs={2}>
            <CustomInput type="switch" label="Transportador" id="transportador" name="transportador" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput type="switch" label="Operadora de Cartão" id="operadoraCartao" name="operadoraCartao" innerRef={formProps.register} />
          </Col>
        </Row>
      </ReportFormView>
    </WindowComponent>
  );
}
